  text-align: left;
  width: 13rem;
  background-color: transparent;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 2; }
  @media screen and (min-width: 25rem) {
    .sidebar {
      width: 15rem; } }
  @media screen and (min-width: 31.25rem) {
    .sidebar {
      width: 18.75rem; } }

/* button --- */
.sidebar__button {
  margin-left: 1.6rem;
  position: absolute;
  top: 17.6rem;
  z-index: 1; }

/* ===================================== 
Sidebar Overlay
===================================== */
.sidebarOverlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5); }

/* ===================================== 
Mobile Navigation Bar
===================================== */
.mobileNavbar {
  height: 100%;
  background: white;
  position: relative;
  z-index: 1; }

/* --- title --- */
.mobileNavbar__title {
  position: absolute;
  left: -100rem; }

/* ===================================== 
Mobile Navigation Menu
===================================== */
.mobileNavMenu {
  margin: 0; }

/* --- link --- */
.mobileNavMenu__link {
  display: block;
  text-decoration: none;
  color: #2E3034;
  padding: 1rem 0 1rem 3rem;
  transition: .2s background-color; }
  .mobileNavMenu__link:hover, .mobileNavMenu__link:focus {
    background-color: #d3e9fb; }

/* Shades of grey --- */
.App {
  position: relative;
  overflow: hidden; }
  .App::before {
    content: '';
    position: absolute;
    top: 28%;
    right: 0;
    z-index: -1;
    width: 97%;
    height: 35rem;
    background-color: #F0F6F6;
    -webkit-transform-origin: 100% 0;
            transform-origin: 100% 0;
    -webkit-transform: rotate(-24deg);
            transform: rotate(-24deg); }

